.cursor {
    cursor: pointer;
}
.vc--resume-flag {
    width: 24px;
}

.vc--resume {
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
ul {
    list-style: none;
}
@media screen and (min-width: 960px) {
    .vc--resume {
        max-width: 1100px;
        margin: 15px auto;
    }
}
@media print {
    body {
        background: transparent;
    }
    .vc--resume {
        background: transparent;
        width: 1000px;
        margin-top: 0;
    }
}
@media screen and (min-width: 1140px) {
    .vc--toolbar {
        position: fixed;
        right: 0;
        top: 30%;
        z-index: 2;
        max-width: 90px;
    }
    .vc--toolbar li {
        margin-bottom: 10px;
        border-radius: 10px 0 0 10px;
        box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
            0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
        background-color: #00bfa5;
        cursor: pointer;
    }
    .vc--toolbar li a {
        display: inline-block;
        width: 80px;
        height: 38px;
        line-height: 38px;
        padding-left: 10px;
        color: rgba(255, 255, 255, 0.8);
        transition: all 0.3s ease-out;
    }
    .vc--toolbar li a:hover {
        color: #ffffff;
    }
}
@media screen and (max-width: 1140px) {
    .vc--toolbar {
        display: none;
    }
}

.left {
    padding: 15px 15px;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    background-color: #00bfa5;
    height: 100%;
}
.left .bottom {
    border-bottom: none !important;
}
.left .avatar {
    text-align: center;
}
.left .avatar img {
    width: 160px;
    height: 160px;
    border-radius: 50%;
    vertical-align: middle;
    object-fit: cover;
}
.left .user-name {
    text-align: center;
    font-size: 20px;
    color: #07020c;
}
.left .slogan {
    font-size: 12px;
    line-height: 1.5;
    color: #524d4d;
    text-align: center;
}
.left .info {
    margin-bottom: 25px;
    border-bottom: 1px solid #abc;
}
.left .info .tag {
    position: relative;
    font-size: 18px;
    color: #204caf;
}
.left .info .tag:before {
    padding-right: 5px;
}
.left .info ul {
    list-style: none;
    padding: 0;
}
.left .info li {
    color: #fff;
    padding: 10px 0 0 20px;
    cursor: default;
}
.left .info li a {
    color: #fff;
}
.left .info .self-evaluation {
    color: #fff;
    text-indent: 18px;
    margin-top: 15px;
    line-height: 1.5;
    word-wrap: break-word;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 7;
    -webkit-box-orient: vertical;
}
.left .social-contact-list li:last-child:before {
    padding-right: 2px;
}
.left .skills li {
    position: relative;
}
.left .skills .skill-name {
    position: relative;
    top: -3px;
    width: 100%;
    font-size: 14px;
    display: inline-block;
    word-break: normal;
}
.left .skills .skill-bar-wrap {
    height: 17px;
    width: 100%;
    background: #00796b;
    display: inline-block;
    border-radius: 20px;
}
.left .skills .skill-bar-wrap .skill-bar {
    height: 17px;
    background: #fff;
    border-radius: 20px 0 0 20px;
}
@media screen and (max-width: 768px) {
    .left {
        display: none;
    }
}

.vc--resume-header {
    padding: 30px 15px;
    background-color: #00bfa5;
    border-radius: 4px;
    box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.5);
}
.vc--resume-header .avatar img {
    width: 160px;
    height: 160px;
    border-radius: 50%;
    vertical-align: middle;
}
.vc--resume-header li {
    font-style: normal;
    font-size: 18px;
    word-break: break-all;
}
.vc--resume-header li a {
    color: rgba(255, 255, 255, 0.8);
    transition: all 0.3s ease-out;
    padding-left: 5px;
    vertical-align: middle;
}
.vc--resume-header li a:hover {
    color: #ffffff;
}
.vc--resume-header li:before {
    color: rgba(255, 255, 255, 0.9);
}
.vc--resume-header .contact ul {
    list-style: none;
    padding: 0;
}
.vc--resume-header .title {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.vc--resume-header .title .user-name {
    font-size: 40px;
    color: rgba(255, 255, 255, 0.8);
    margin: 5px 0;
}
.vc--resume-header .title .job-title {
    font-size: 36px;
    color: rgba(255, 255, 255, 0.8);
    margin: 5px 0;
}
.vc--resume-content article {
    background-color: #f0f2f1;
    border-radius: 4px;
    box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.5);
}
.vc--resume-content article.active {
    transform: rotateY(0deg);
}
.vc--resume-content article.inactive {
    transform: rotateY(180deg);
    display: none;
}
.vc--resume-content article .right {
    padding: 15px 20px;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
}
.vc--resume-content article .right section {
    margin-bottom: 25px;
}
.vc--resume-content article .right section .content {
    margin: 15px 0 10px 10px;
    font-size: 17px;
}
.vc--resume-content article .right section .content .information {
    width: 100%;
}
.vc--resume-content article .right section .title-wrap {
    font-size: 22px;
    color: #00796b;
    line-height: 30px;
    border-bottom: 1px solid #00796b;
}
.vc--resume-content article .right section .title-wrap i {
    color: #1c4684;
    font-size: 16px;
}

.vc--resume-content article .right section .title-wrap span {
    padding-left: 7px;
}
.vc--resume-content article .right section.education .content span {
    padding: 0 5px;
}
.vc--resume-content article .right section.skill-desc ul {
    list-style: none;
}
.vc--resume-content article .right .skill-desc ul.skill-desc {
    list-style: none;
}
.vc--resume-content article .right .skill-desc ul.skill-desc li {
    padding-bottom: 5px;
    font-size: 15px;
    line-height: 1.5;
}
.vc--resume-content article .right .skill-desc ul.skill-desc li:last-child {
    padding-bottom: 0;
}
.vc--resume-content article .right .skill-desc ul.skill-desc li:before {
    font-size: 14px;
}
.skills {
    list-style: none;
}
.skills li {
    position: relative;
    margin-bottom: 10px;
}
.skills .skill-name {
    position: relative;
    top: -3px;
    width: 100%;
    font-size: 14px;
    display: inline-block;
    word-break: normal;
}
.skills .skill-bar-wrap {
    padding: 0;
    margin: 0;
    height: 17px;
    background: #00796b;
    display: inline-block;
    border-radius: 20px;
}
.skills .skill-bar-wrap .skill-bar {
    height: 17px;
    background: #fff;
    border-radius: 20px;
    margin-left: -1px;
}

.vc--resume-main-footer span {
    cursor: pointer;
    display: inline-block;
    padding: 6px 12px;
    border-radius: 4px;
    color: rgba(0, 191, 165, 0.8);
}
.vc--resume-main-footer span:hover {
    color: #00bfa5;
}
.vc--resume-main-footer a {
    cursor: pointer;
    display: inline-block;
    padding: 6px 12px;
    border-radius: 4px;
    color: rgba(0, 191, 165, 0.8);
}
.vc--resume-main-footer a:hover {
    color: #00bfa5;
}
@media screen and (min-width: 768px) and (max-width: 940px) {
    .vc--resume-content article .right section .content {
        margin-left: 0;
    }
}
@media screen and (max-width: 625px) {
    .vc--resume-content
        article
        .right
        section.education
        .content
        span:nth-child(3) {
        display: none;
    }
}
@media screen and (max-width: 414px) {
    .vc--resume-content article .right section .content {
        margin: 15px 0 10px 0px;
    }
    .vc--resume-content
        article
        .right
        section.education
        .content
        span:last-child {
        display: none;
    }
}

ul.project-desc {
    list-style: none;
}
.timeline {
    flex: 1;
    display: flex;
    flex-direction: column;
}
.timeline .timeline-item {
    position: relative;
    display: flex;
    padding-bottom: 18px;
}
.timeline .timeline-item .timeline-item-time {
    flex: 1;
    color: #3b3b3b;
    padding-top: 3px;
}
.timeline .timeline-item .timeline-item-project {
    position: relative;
    flex: 3;
    padding-left: 15px;
}
.timeline .timeline-item .timeline-item-project .circle {
    position: absolute;
    left: -8px;
    top: 8px;
    height: 14px;
    width: 14px;
    border-radius: 100%;
    background: #00796b;
}
.timeline .timeline-item .timeline-item-project .project-info {
    display: flex;
    align-items: center;
    color: #00bfa5;
}
.timeline .timeline-item .timeline-item-project .project-info .project-name {
    display: inline-block;
    font-weight: bold;
}
.timeline .timeline-item .timeline-item-project .project-info .project-code {
    font-size: 12px;
    color: #4db6ac;
    border: 1px solid #4db6ac;
    padding: 2px 5px;
    border-radius: 5px;
    margin-left: 15px;
}
.timeline .timeline-item .timeline-item-project .project-info .project-time,
.timeline .timeline-item .timeline-item-project .project-info .project-link {
    display: none;
}
.timeline .timeline-item .timeline-item-project .project-info .work-title {
    display: inline-block;
    font-weight: bold;
    margin-left: 20px;
}
.timeline .timeline-item .timeline-item-project .project-desc {
    padding-top: 10px;
    padding-left: 15px;
    border-left: 2px solid #00796b;
    margin-left: -17px;
}
.timeline .timeline-item .timeline-item-project .project-desc ul {
    list-style: none;
}
.timeline .timeline-item .timeline-item-project .project-desc li {
    padding-bottom: 5px;
    font-size: 15px;
    line-height: 1.5;
}
.timeline .timeline-item .timeline-item-project .project-desc li:last-child {
    padding-bottom: 0;
}
.timeline .timeline-item .timeline-item-project .project-desc li:before {
    font-size: 14px;
}
.timeline .timeline-item .timeline-item-project .project-desc li .other-print {
    display: none;
}
.timeline .timeline-item .timeline-item-project .project-desc li .project-code {
    font-size: 12px;
    color: #4db6ac;
    border: 1px solid #4db6ac;
    padding: 2px 5px;
    border-radius: 5px;
    margin-left: 15px;
}
.timeline .timeline-item .timeline-item-project .work-project-list {
    border-left: 2px solid #00796b;
    margin-left: -17px;
    margin-top: 1px;
}
.timeline .timeline-item .timeline-item-project .work-project-list h4 {
    padding-top: 10px;
    padding-left: 15px;
    font-weight: bold;
}
.timeline
    .timeline-item
    .timeline-item-project
    .work-project-list
    .project-desc {
    border: none;
    margin-left: 0;
}
.timeline
    .timeline-item
    .timeline-item-project
    .work-project-list
    .project-desc
    ul {
    list-style: none;
}
@media screen and (max-width: 695px) {
    .timeline .timeline-item .project-desc .project-code {
        display: none;
    }
    .timeline .timeline-item .project-desc ul {
        list-style: none;
    }
    .timeline .timeline-item .timeline-item-time {
        display: none;
    }
    .timeline
        .timeline-item
        .timeline-item-project
        .project-info
        .project-code {
        display: none;
    }
    .timeline
        .timeline-item
        .timeline-item-project
        .project-info
        .project-time {
        display: inline-block;
        margin-left: 10px;
    }
}
@media screen and (max-width: 414px) {
    .timeline .timeline-item .timeline-item-time {
        display: none;
    }
    .timeline .timeline-item .timeline-item-project .project-info .work-title {
        display: none;
    }
}
@media print {
    .timeline .project-desc {
        margin-top: 10px;
    }
    .timeline .project-desc .project-code {
        display: none;
    }
    .timeline .timeline-item {
        position: relative;
        display: flex;
        padding-bottom: 18px;
    }
    .timeline .timeline-item .timeline-item-time {
        flex: 1;
        color: #3b3b3b;
        padding-top: 3px;
    }
    .timeline .timeline-item .timeline-item-project .circle {
        display: none;
    }
    .timeline .timeline-item .timeline-item-project .project-info {
        margin-left: -20px;
    }
    .timeline .timeline-item .timeline-item-project .project-info a {
        display: none;
    }
    .timeline
        .timeline-item
        .timeline-item-project
        .project-info
        .project-link {
        display: inline-block;
    }
    .timeline
        .timeline-item
        .timeline-item-project
        .project-desc
        li
        .other-print {
        display: inline-block;
    }
    .timeline .timeline-item .timeline-item-project .work-project-list,
    .timeline .timeline-item .timeline-item-project .project-desc {
        margin-top: 7px;
    }
}

.pl-5 {
    padding-left: 5px;
}
